











































html{
  /*IE scrollbar*/
  scrollbar-arrow-color: #3397b3;
  scrollbar-face-color: #3397b3;
  scrollbar-3dlight-color: #3397b3;
  scrollbar-highlight-color: #3397b3;
  scrollbar-shadow-color: #3397b3;
  scrollbar-darkshadow-color: #0a0a0a;
  scrollbar-track-color: #324550;
  scrollbar-base-color:#121212;
}
/*chrome scrollbar*/
::-webkit-scrollbar {width: 8px;height:8px;}
::-webkit-scrollbar-track {-webkit-box-shadow: inset 0 0 6px #28c6de;border-radius: 6px;}
::-webkit-scrollbar-track-piece {background: #314552;}
::-webkit-scrollbar-thumb {border-radius: 6px;background: #28c6de;-webkit-box-shadow: inset 0 0 6px #28c6de;}
::-webkit-scrollbar-thumb:window-inactive {background: rgba(31, 67, 85, 0.4);}
::-webkit-scrollbar-corner  {background:#314552;}

body {
  font-size: 14px !important;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif !important;
  color: #fff;
  background: #075760 url(assets/bg.svg) no-repeat fixed top center/cover;
}
input, button {
  color: #fff;
}

* {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}
*:before,
*:after {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
}

.btn {
  color: #28c6df;
  border: 1px solid #28c6df;
  background: rgba(40,198,223,.2);
  cursor: pointer;
  outline: none;
}
.btn:hover {
  background: rgba(40,198,223,.4);
}
.btn[disabled] {
  border-color: rgba(255,255,255,.2);
  background: rgba(255,255,255,.1);
}


.fade-transition {
  -webkit-transition: opacity .6s ease;
  transition: opacity .6s ease;
  opacity: 1;
}
.fade-enter, .fade-leave {
  opacity: 0;
}



